export default {
	data() {
		return {
			originInfo: {
				name: "未登录/注册",
				detail: "点击头像可登陆、注册",
				avatar: "../../static/4.jpg"
			},
			settingList: [
				{ name: '在线简历', class: 'icon-feeds', url: '../resumeOnline/resumeOnline'},
				{ name: '附件简历', class: 'icon-folder', url: '../resumeAttachment/resumeAttachment'},
				{ name: '投递记录', class: 'icon-history', url: '../history/history'},
				{ name: '我的收藏', class: 'icon-collection', url: '../collect/collect'},
				{ name: '设置', class: 'icon-zhanghucaozuo', url: '../setting/setting'}
			],
			userInfo: null
		}
	},
	onShow() {
		this.userInfo = uni.getStorageSync('userInfo')
	},
	computed: {
		nowUserInfo() {
			if(this.userInfo) {
				return this.userInfo
			}else {
				return this.originInfo
			}
		}
	},
	methods: {
		handleUserInfo(e) {
			console.log(e)
			if(e.target.userInfo) {
				const info = e.detail.userInfo
				const _userInfo = {
					name: info.nickName,
					detail: info.province + " " +info.city,
					avatar: info.avatarUrl
				}
				// this.$store.dispatch('setUserInfo', _userInfo)
				this.userInfo = _userInfo
				uni.setStorageSync('userInfo', _userInfo)
			}else {
				return
			}
		},
		handleLogout() {
			uni.showModal({
				content: "确认退出当前账号？",
				confirmColor: "#3a3cd7",
				success: (res) => {
					if(res.confirm) {
						uni.removeStorageSync('userInfo')
						this.userInfo = null
						// this.$store.dispatch('setUserInfo', null)
						uni.showToast({
							title: "退出成功"
						})
					}
				}
			})
		}
	}
}
